#aboutMain {
	justify-content: center;
	text-align: center;
	position: fixed;
	width: 100%;
	height: 100%;
	overflow: auto;
	scroll-behavior: smooth;
}

#logo {
	max-width: 100%;
	max-height: 100%;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.alignLeft {
	text-align: left;
}

.links {
	margin: 20px 0px 0px 10px;
	height: 100%;
	position: fixed;
	text-align: left;
	overflow: scroll;
	background-color: #fff;
	z-index: 1;
}

.indented {
	margin-left: 35px;
}

.regulatedWidth {
	max-width: 500px;
	min-width: 100px;
	margin: auto;
}

.boldText {
	font-weight: bold;
}

.largeText {
	font-size: 25px;
}

.spaced {
	margin: 50px;
}

.unstyledList {
	list-style-type: none;
}

a {
	color: #000;
}

a:hover {
	color: #09f;
}

/*https://www.formget.com/css-hr/*/
hr.styled {
	padding: 0;
	border: none;
	border-top: medium double #333;
	color: #333;
	text-align: center;
}

hr.styled:after {
	content: "ðŸžžðŸž›ðŸžž";
	display: inline-block;
	position: relative;
	top: -0.8em;
	font-size: 1.5em;
	padding: 0 0.25em;
	background: white;
}

.hideShow {
	top: 0;
	left: 0;
	transition: 350ms;
}

.hideShow.hidden {
	left: -100%;
	transition: 350ms;
}

.attach {
	position: fixed;
	z-index: 2;
}